.display {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5rem;

  @media (max-width: 600px) {
    flex: 1;
  }
  @media (max-width: 400px) {
    padding: 2rem;
  }

  .title {
    font-size: 2.5rem;
    font-weight: 300;
    margin-bottom: 1.5rem;
    text-align: center;
  }

  .budget {
    font-size: 4.6rem;
    font-weight: 300;
    text-align: center;
    margin-bottom: 1.5rem;
  }

  .income {
    margin-bottom: 20px;
  }

  .income,
  .expenses {
    display: flex;
    align-items: center;
    padding: 1.2rem;
    border-radius: var(--border-radius);
    font-size: 1.8rem;
    background-color: rgba(var(--color-rgb), 0.8);

    .text {
      flex: 1;
    }
    .value {
      margin-right: 0.5rem;
    }
    .percentage {
      font-size: 1.3rem;
      width: 3.4rem;
      padding: 0.5rem;
      border-radius: var(--border-radius);
      background-color: rgba(var(--color-rgb), 0.2);
      text-align: center;
    }
  }
}
